:root {
    --Soft-blue: hsl(215, 51%, 70%);
    --Cyan: hsl(178, 100%, 50%);
    --Very-dark-blue: hsl(217, 54%, 11%);
    --Very-dark-blue-2: hsl(216, 50%, 16%);
    --Very-dark-blue-3: hsl(215, 32%, 27%);
    --White: hsl(0, 0%, 100%);
}

.container {
    text-align: left;
    padding: 30px;
    background-color: var(--Very-dark-blue);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.card {
    display: flex;
    flex-direction: column;
    background-color: var(--Very-dark-blue-2);
    padding: 30px;
    border-radius: 15px;
    align-items: center;
    gap: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    align-items: flex-start;
}

.desc {
    text-align: left;
    color: var(--Soft-blue);
    width: 90%;
    align-items: flex-start;
}

h2 {
    text-align: left;
    color: white;
    width: 60%;
    font-size: 25px;
}

h2:hover {
    color: var(--Cyan);
    cursor: pointer;
}

.eq {
    border-radius: 10px;
    width: 100%;
    z-index: 1;
    display: block; 
}

.equilibrium {
    position: relative;
    display: inline-block;
}

.overlay {
    position: absolute;
    transition: all .3s ease;
    opacity: 0;
    background-color: #eee;
}

.container:hover .overlay {
    opacity: 1;
}

.text {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 25px;
}

.overlayLeft {
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    background-color: rgba(0, 255, 255, 0.692);
    cursor: pointer;
}

.container:hover .overlayLeft {
    width: 100%;
}

.c-t {
    display: flex;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px rgba(255, 255, 255, 0.247) solid;
    align-items: center;
    padding-bottom: 20px;
}

.coins {
    display: flex;
    gap: 5px;
    color: var(--Cyan);
}

.time {
    display: flex;
    gap: 8px;
    color: var(--Soft-blue);
}

.time img {
    width: 22px;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    color: var(--Soft-blue);
}

footer img {
    width: 40px;
    border: 1px white solid;
    border-radius: 50px;
}

span {
    color: white;
}

span:hover {
    color: var(--Cyan);
    cursor: pointer;
}

